
import { defineComponent,onMounted,reactive,toRefs,inject,onBeforeUnmount } from 'vue'
import { getJson } from '@/api/apiConfig'
import { gettopalbum } from '@/api/api'
import { addDate2,myDate,goPage } from '@/utils/common'
import { useRouter } from 'vue-router'
import LoadingCpn from "@/components/common/loadingcpn.vue"
interface state{
    optionList:{name:string,data:string}[],
    optionarea:string,
    albumList:any[],
    albumOn:number,
    today: string,
    timeDown:number,
    loading:boolean,
    optionType:string
}
export default defineComponent({
    name:'handtailor',
    components:{
        LoadingCpn
    },
    setup () {
        let state = reactive<state>({
            optionList:[
                {
                    name: '',
                    data: 'ALL',
                },
                {
                    name: '',
                    data: 'ZH',
                },
                {
                    name: '',
                    data: 'EA',
                },
                {
                    name: '',
                    data: 'JP',
                },
                {
                    name: '',
                    data: 'KR',
                }
            ],
            optionarea:'ALL',
            albumList:[],
            albumOn:-1,
            today: myDate(),
            timeDown:0,
            loading:false,
            optionType:'new'
        })
        let newalbumLeftData:any = inject('newalbumLeftData')
        let changearea = (data:string) => {
            state.optionarea = data
            state.albumList = []
            state.albumOn = -1
            state.timeDown = 0
            getData()
        }
        let changeType = (data:string) => {
            state.optionType = data
            state.albumList = []
            state.albumOn = -1
            state.timeDown = 0
            getData()
        }
        let getData = () => {
            let year = addDate2(state.today,state.timeDown).split('-')[0]
            let month = addDate2(state.today,state.timeDown).split('-')[1]
            state.loading = true
            getJson(gettopalbum,{year:year,month:month,area:state.optionarea,type:state.optionType},(res:{code:number,weekData:any[],monthData:any[]}) => {
                state.loading = false
                if(res.code == 200){
                    let weekobj:{timedate:string,data:any[]} = {
                        timedate:'',
                        data:[]
                    }
                    let obj:{timedate:string,data:any[]} = {
                        timedate:'',
                        data:[]
                    }
                    if(res.weekData && res.weekData.length > 0){
                        weekobj.timedate = 'weekData',
                        weekobj.data = res.weekData
                        state.albumList.push(weekobj)
                    }
                    if(res.monthData && res.monthData.length > 0){
                        obj.timedate = year + '-' + month,
                        obj.data = res.monthData
                        state.albumList.push(obj)
                    }
                }
            },(err:any) => {

            })
        }
        onMounted(() => {
            const el:HTMLElement = document.querySelector(".index-screen") as HTMLElement
            const offsetHeight = el.offsetHeight
            el.onscroll = () => {
                const albumDatedom = document.getElementsByClassName("newalbum-left")
                Array.prototype.forEach.call(albumDatedom,function(el,index){
                    let domRect = el.getBoundingClientRect()
                    // console.log(dom)
                    if(domRect.top < 200){
                        state.albumOn = index
                        newalbumLeftData.newalbumOn = true
                        if(index == 0){
                            newalbumLeftData.timedate = 'weekData'
                        }else{
                            newalbumLeftData.timedate = el.getElementsByClassName('year')[0].innerHTML + '-' + el.getElementsByClassName('month')[0].innerHTML
                        }
                    }
                    if(domRect.top > 110 && index == 0){
                        state.albumOn = -1
                        newalbumLeftData.newalbumOn = false
                    }
                })
                const scrollTop = el.scrollTop
                const scrollHeight = el.scrollHeight
                if(offsetHeight + scrollTop - scrollHeight >= -10000 && !state.loading){
                    state.timeDown--
                    getData()
                }
            }
        })
        onBeforeUnmount(() => {
            newalbumLeftData.newalbumOn = false
        })
        getData()
        const router = useRouter()
        return {
            ...toRefs(state),
            changearea,
            router,
            goPage,
            changeType
        }
    }
})
