
import { defineComponent,toRefs } from 'vue'
import { useRouter } from 'vue-router'
export default defineComponent({
  	name:'titlemm',
  	components:{
		
  	},
	props:{
		title: String,
		arrow: Boolean,
		href: String,
	},
  	setup(props){
		const { title,arrow,href } = toRefs(props)
		const router = useRouter()
		function goUrl(href:string) {
			if(href){
				router.push(href)
			}
		}
		return{
			title,
			arrow,
			href,
			goUrl
		} 
  	}
})
