
import { defineComponent,reactive,toRefs } from 'vue'
import { option } from './optiondata'
interface state{
    selectOption:any[],
    params:{
        languages: string,
        classify: string,
        screen: string,
    }
}
export default defineComponent({
    name:'handtailor',
    setup (props,context) {
        let state = reactive<state>({
            selectOption:option,
            params:{
                languages:'-1',
                classify:'-1',
                screen:'',
            }
        })
        let changeOption = (type:string,data:string) => {
            switch (type) {
                case 'languages':
                    state.params.languages = data
                    context.emit('notice',state.params)
                    break;
                case 'classify':
                    state.params.classify = data
                    context.emit('notice',state.params)
                    break;
                case 'screen':
                    state.params.screen = data
                    context.emit('notice',state.params)
                    break;
                default:
                    break;
            }
        }
        context.emit('notice',state.params)
        return {
            ...toRefs(state),
            changeOption
        }
    }
})
