
import { defineComponent,onMounted,reactive,toRefs } from 'vue'
import { getJson } from '@/api/apiConfig'
import { sddetail } from '@/api/api'
import { useRouter } from 'vue-router'
import LoadingCpn from "@/components/common/loadingcpn.vue"
import { goPage,audioPlay } from "@/utils/common"
import {useStore} from 'vuex'
interface state{
    dataList: any[],
    trackIds: any[],
    OfficialName: string,
    songsheetId: string,
}
interface playlist{
    tracks: any[],
    trackIds: any[],
}
interface SongInfoGet{
    id: string,
    name: string,
    al: {picUrl:string},
    ar: any[],
    dt: number
}
interface SongInfoSet{
    id: string,
    name: string,
    picUrl: string,
    song:{artists: any[]}
}
export default defineComponent({
    name:'officialplaylist',
    props:['id','OfficialName'],
    components:{
        LoadingCpn
    },
    setup (props) {
        let state = reactive<state>({
            dataList:[],
            trackIds:[],
            OfficialName: props.OfficialName,
            songsheetId: props.id,
        })
        let getData = () => {
            getJson(sddetail,{id:props.id},(res:{code:number,playlist:playlist}) => {
                if(res.code == 200) {
                    state.dataList = res.playlist.tracks
                    state.trackIds = res.playlist.trackIds
                }
            },(err:any) => {

            })
        }
        const store = useStore()
        let goAudioPlay = (data:SongInfoGet) => {
            let SongInfo:SongInfoSet = {
                id: data.id,
                name: data.name,
                picUrl: data.al.picUrl,
                song:{
                    artists: data.ar
                }
            }
            let arr:any[] = []
            let forArr:any[] = []
            forArr = JSON.parse(JSON.stringify(state.dataList))
            forArr.forEach(item => {
                let obj = {
                    id: item.id,
                    name: item.name,
                    picUrl: item.al.picUrl,
                    song:{
                        artists: item.ar,
                        bMusic:{
                            playTime: item.dt,
                        }
                    }
                }
                arr.push(obj)
            })
            audioPlay(SongInfo,arr,store)
        }
        onMounted(() => {
          getData()  
        })
        let router = useRouter()
        return {
            ...toRefs(state),
            router,
            goPage,
            goAudioPlay
        }
    }
})
