
import { defineComponent,reactive,toRefs } from 'vue'
import { getJson } from '@/api/apiConfig'
import { gettopsong } from '@/api/api'
import { useRouter } from 'vue-router'
import { playtime,goPage,audioPlay } from '@/utils/common'
import {useStore} from 'vuex'
import LoadingCpn from "@/components/common/loadingcpn.vue"
interface state{
    optionList: {name:string,data:number}[],
    optionType: number,
    dataList: any[],
    loading: boolean
}
interface audioInfoGet{
    id: string,
    name: string,
    album: {
        picUrl:string,
    },
    artists:any[],
    duration?: number
}
interface audioInfoSet{
    id: string,
    name: string,
    picUrl: string,
    song: {
        artists: any[]
    }
}
export default defineComponent({
    name:'newsong',
    components:{
        LoadingCpn
    },
    setup () {
        let state = reactive<state>({
            optionList:[
                {
                    name: '',
                    data: 0,
                },
                {
                    name: '',
                    data: 7,
                },
                {
                    name: '',
                    data: 96,
                },
                {
                    name: '',
                    data: 8,
                },
                {
                    name: '',
                    data: 16,
                }
            ],
            optionType:0,
            dataList:[],
            loading:false,
        })
        let changeOption = (data:number) => {
            state.optionType = data
            getData()
        }
        let getData = () => {
            state.loading = true
            getJson(gettopsong,{type:state.optionType},(res:{code:number,data:any[]}) => {
                state.loading = false
                if(res.code == 200){
                    state.dataList = res.data
                }
            },(err:any) => {

            })
        }
        getData()
        const router = useRouter()
        const store = useStore()
        let goAudioPlay = (data:audioInfoGet) => {
            let SongInfo:audioInfoSet = {
                id: data.id,
                name: data.name,
                picUrl: data.album.picUrl,
                song:{
                    artists: data.artists
                }
            }
            let arr:any[] = []
            let forArr = []
            forArr = JSON.parse(JSON.stringify(state.dataList))
            forArr.forEach((item:audioInfoGet) => {
                let obj = {
                    id: item.id,
                    name: item.name,
                    picUrl: item.album.picUrl,
                    song:{
                        artists: item.artists,
                        bMusic:{
                            playTime: item.duration,
                        }
                    }
                }
                arr.push(obj)
            })
            audioPlay(SongInfo,arr,store)
        }
        return {
            ...toRefs(state),
            playtime,
            router,
            goPage,
            goAudioPlay,
            changeOption
        }
    }
})
